.main{
  margin: 0 auto;
  text-align: center;
}
.area{
  position: absolute;
  margin-right: 25%;
  margin-left: 25%;
  margin-top: 9%;
  width: 50%;
  height: 57%;
  background-color: #000;
  border: 2px solid #000;
}
.main-dot{
  position: absolute;
  border-radius: 50%;
  width:2%;
  height: 3%;
  background-color: rgb(196, 29, 29);
  border: 1px solid #fff;
  z-index: 2;
}

.main-food{
  position: absolute;
  width: 2%;
  height: 3%;
  border-radius: 50%;
  background-color: darkgreen;
  border: 1px solid #fff;
  z-index: 1;
}
.footer{
  position: absolute;
  /* margin-top: 20%; */
  color: red;
  font-size: xx-large;
}
.head{
